.site-header {
  border-bottom: 1px solid #e1e1e1;
  padding: 8px 0 8px 0;
}

.fixed-nav {
  background-color: rgba($color: white, $alpha: 0.4);
  // position: fixed;
  width: 100%;
  height: 40px;
  z-index: 5;

  .header-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-content: center;
    @include main-container-size;
    grid-template: [row1-start] 'logo . nav-menu' 1fr [row1-end];

    .site-description {
      clip: rect(1px, 1px, 1px, 1px);
      position: absolute !important;
      height: 1px;
      width: 1px;
      overflow: hidden;
    }
    .site-description:focus {
      clip: auto !important;
      display: block;
      height: auto;
      left: 5px;
      top: 5px;
      width: auto;
      z-index: 100000; /* above all content */
    }

    .site-branding {
      width: 50px;
    }
    .main-navigation {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: flex-end;
      width: 60%;

      .search-form {
        display: flex;
        flex-direction: row;
        align-items: center;

        label {
          order: 2;
        }
        fieldset {
          border: none;
          a {
            padding: 0 12px 0 12px;
          }
        }
      }

      input[type='search'] {
        order: 2;
        display: none;
      }

      a {
        text-decoration: none;
        text-transform: uppercase;
        color: #248a83;
        line-height: 1.75;
        padding-left: 24px;

        &:hover {
          color: #2f1339;
        }
      }
    }
  }
}
